.pic{
  display: block;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
}

.title, .name, .site{
text-align: center;
}

.title{
  color: #F3BF99;
  font-size: 1.3rem;
  margin: 0 0 .5em;
}

.name{
  font-size: 2.3rem;
  margin: .5em 0 0 0;
}

.site{
  display: block;
  color: #F5F5F5;
  text-decoration: none;
  margin: 0 auto;
}

.site:visited{
  color: #F5F5F5;
}

h1, h2, h3 {
  color: #F5F5F5;

}

.about p{
  color: #DCDCDC;
}

.contact{
  margin: 0 auto;
  margin-top: 1em;
  width: 78%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.email, .linkedin{
  display: flex;
  gap:0.5em;
  padding: .5em;
  width: 100px;
  border-radius: 3px;
  justify-content: center;
}

.email-link{
  color: #161619;
  text-decoration: none;
}

.email{
  background-color: #FFFFFF;
}

.linkedin{
  background-color: #5093E2;
}

.linkedin-link{
  color: #FFFFFF;
  text-decoration: none;
}

.about{
  width: 78%;
  margin: 0 auto;
}

.about h3{
  font-size: 1.3em;
  margin-bottom: 0.4em;
}

.about p{
  margin: 0;
  line-height: 1.5em;
  font-size: 13px;
}

.footer{
  display: flex;
  justify-content: center;
  gap: 2em;
  margin-top: 1em;
  padding: 1.7em 0;
  background-color: #161619;
}

@media (min-width:600px){
  .pic{
    border-radius: 50%;
    text-align: center;
    border: 2px solid black;
  }
  .contact{
    margin-top: 1em;
    width: 78%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 2em;
  }

  .about h3{
    font-size: 1.5rem;
  }
  
  .about p{
    font-size: 1rem;
  }

}